.btn {
    min-width: 130px;
    height: 40px;
    color: #fff;
    border-radius: 6px;
    padding: 5px 10px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    outline: none;
    border: 1px solid #000;
    color: beige;
    background: transparent;
    z-index: 0;
  }
  .btn:hover:after {
    top: 0;
    left: 0;
  }
  .btn:after {
    content: "";
    width: 100%;
    z-index: -1;
    border-radius: 6px;
    position: absolute;
    height: 100%;
    top: 5px;
    left: 5px;
    transition: 0.2s;
    background-color: #ff6392;
  }